import axios from 'axios';
import { utilService } from './util.service';
const BTC_RATE_KEY = 'bitcoin-rate';
const MARKET_PRICE_KEY = 'market-price';

export const bitcoinService = {
  getRate,
  getMarketPrice,
};

async function getRate() {
  try {
    var rate = utilService.loadFromStorage(BTC_RATE_KEY);
    if (!rate) {
      const res = await axios.get('https://blockchain.info/tobtc?currency=USD&value=1');
      rate = res.data;
      utilService.saveToStorage(BTC_RATE_KEY, rate);
    }
    return rate;
  } catch (error) {
    console.log("Couldn't get bitcoin rate", error);
  }
}

async function getMarketPrice() {
  try {
    var prices = utilService.loadFromStorage(MARKET_PRICE_KEY);
    if (!prices || !prices.length) {
      const res = await axios.get('https://api.blockchain.info/charts/market-price?timespan=5months&format=json&cors=true');
      prices = res.data;
      utilService.saveToStorage(MARKET_PRICE_KEY, prices);
    }
    return prices;
  } catch (error) {
    console.log("Couldn't get market price", error);
  }
}
