import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

export class AppHeader extends Component {
    render() {
        return (
            <div className="main-header">
                <p className="logo">Yaniv-Coin</p>
                <ul>
                 <li>  <NavLink activeClassName="active-nav" exact to="/homepage">Home</NavLink> </li>
                 <li>  <NavLink activeClassName="active-nav" exact to="/contacts">Contacts</NavLink> </li>
                 <li>  <NavLink activeClassName="active-nav" exact to="/statistics">Statistics</NavLink> </li>
                </ul>
            </div>
        )
    }
}