import { Component } from 'react'
import { Link } from 'react-router-dom'
import { ContactDetails } from '../cmps/ContactDetails'
import { contactService } from '../services/contact.service'
import { TransferFunds } from '../cmps/TransferFunds'

export class ContactDetailsPage extends Component {
    state = {
        contact: null,
    }

    componentDidMount() {
        this.loadContact()
    }

    loadContact = async () => {
        const { id } = this.props.match.params;
        const contact = await contactService.getContactById(id)
        // const nextPrevId = await contactService.getNextPrevId(id)
        // this.setState({contact, nextPrevId})
        this.setState({ contact })
    }

    deleteContact = async () => {
        const { _id } = this.state.contact;
        await contactService.deleteContact(_id)
        this.goBack()
    }

    goBack = () => {
        this.props.history.push('/contacts')
    }

    handlePagination = async (diff) => {
        const contact = await contactService.getNextPrevContact(diff, this.state.contact._id)
        console.log('contact:', contact)
        this.setState({ contact })
    }

    render() {
        const { contact } = this.state
        if (!contact) return <div>Loading...</div>
        return (
            <div className="contact-details-container">
                      <button onClick={this.goBack}>Back to contacts</button>
                <h2 className="page-title">Contact details</h2>
                <ContactDetails contact={contact} />
                <button onClick={()=>this.handlePagination(-1)}> ← Prev </button>
                <Link to={'/contact/edit/' + contact._id}><button>Edit</button></Link>
                <button onClick={this.deleteContact}>Delete</button>  
                <button onClick={()=>this.handlePagination(1)}>Next →</button>
                {/* <Link to={'/contact/' + this.state.nextPrevId.nextId}>Next</Link>
                <Link to={'/contact/' + this.state.nextPrevId.prevId}>Prev</Link> */}

                <TransferFunds/>
            </div>
        )
    }
}
