import { Component } from 'react'
import { Sparklines, SparklinesLine } from 'react-sparklines';
import { bitcoinService } from '../services/bitcoin.service';


export class StatisticPage extends Component {
    state = {
        marketPrice: []
    }

    componentDidMount() {
        this.loadMarketPrice()
    }

    async loadMarketPrice() {
        const res = await bitcoinService.getMarketPrice()
        const marketPrice = res.values.map(val => val.y)
        this.setState({ marketPrice })
    }

    render() {
        return (
            <div>
                <h2 className="page-title">Bit-Stats</h2>
                <h3>market-price: {parseInt(this.state.marketPrice)}</h3>
                <div className='graph'>
                    <Sparklines data={this.state.marketPrice}>
                        <SparklinesLine color="#22f553" />
                    </Sparklines>
                </div>
            </div>
        )
    }
}
