import { utilService } from './util.service'
export const userService = {
  getUsers,
  signUp,
  updateUser
};
const gUsers = [];

function getUsers() {
  let users = utilService.loadFromStorage('USER-KEY')
  if (!users) {
    users = [];
  }

  return users
}

function signUp(name) {
  gUsers.push({ name, coins: 100, moves: [], id: utilService.makeId() })
  utilService.saveToStorage('USER-KEY', gUsers)
  return gUsers
}

function updateUser(userToUpdate) {
  console.log(userToUpdate)
  let users = getUsers()
  users = users.map(user => {
    if (userToUpdate.id === user.id)
      user = userToUpdate;

    return user
  })
  utilService.saveToStorage('USER-KEY', users)
  return users;
}
