import { Component } from 'react'
import { bitcoinService } from '../services/bitcoin.service'
import { userService } from '../services/user.service'
import { HomePage } from './HomePage'
import { SignUp } from './SignUp'


export class BitcoinApp extends Component {
    state = {
        users: [],
        currUserId: 0,
        BTC: null,
        userName: null
    }

    componentDidMount() {
        this.loadUsers()
        this.loadRate()
    }

    loadUsers() {
        const users = userService.getUsers()
        this.setState({ users: users })
    }


    //sign up

    handleName = ({ target }) => {
        console.log('handling name')
        const field = target.id
        const value = target.value
        console.log(value)
        this.setState(prevState => ({ userName: value }))
    }
    signUp = async (ev) => {
        ev.preventDefault()
        const userName = this.state.userName
        await userService.signUp(userName)
        this.loadUsers()
        this.loadRate()
        console.log('reched the end')
    }




    async loadRate() {
        const rate = await bitcoinService.getRate()
        this.setState({ BTC: rate })
    }

    render() {
        const { users, currUserId, BTC, userName } = this.state

        const currUser = users[currUserId]
            return (
                <section>
                    {currUser && <HomePage user={currUser} BTC={BTC} />}
                </section>

            )

    }
}
