import { Component } from 'react'
import { userService } from '../services/user.service'

export class TransferFunds extends Component {

    state = {
        userFunds: null,
        fundstoTransfer: 0
    }

    componentDidMount() {
        const userFunds = userService.getUsers()[0].coins
        console.log(this)
        this.setState({ userFunds, fundstoTransfer: this.state.fundstoTransfer })
    }

    handleChange = ({ target }) => {
        const field = target.id
        let value = target.value
        value = parseInt(value)
        if (value > this.state.userFunds) {
            alert('not enough funds!')
            this.setState(prevState => ({ fundstoTransfer: 0 }))
        }
        else this.setState({ fundstoTransfer: value })
    }

    transfer = () => {
        const user = userService.getUsers()[0]
        const funds = JSON.parse(JSON.stringify(this.state))
        funds.userFunds = funds.userFunds - funds.fundstoTransfer
        user.coins = funds.userFunds
        userService.updateUser(user)
        this.setState(prevState => ({ fundstoTransfer: 0, userFunds:funds.userFunds }))

    }

    render() {
        return (<div className="transfer-funds"> <h1>   Transfer Funds</h1>
            <p>Your coins : <span> {this.state.userFunds} </span></p>
            <input type="number" value={this.state.fundstoTransfer} onChange={this.handleChange} />

            <button onClick={this.transfer}> Transfer Funds!</button>



        </div>)
    }

}