import { Component } from 'react'
import { Router } from 'react-router-dom'
import { userService } from '../services/user.service'
import { signUp, loadUsers } from '../store/actions/userActions'

export class SignUp extends Component {
    state = {
        userName: '',
        users: []
    }

    componentDidMount() {
        const users = userService.getUsers()
        this.setState({ users: users })
        console.log(users)
        // this.getUsers()
    }

    // getUsers() {
    //     console.log(loadUsers())
    //     return loadUsers();
    // }
    handleChange = ({ target }) => {
        console.log(this.state)
        const field = target.id
        const value = target.value
        console.log(value)
        this.setState(prevState => ({ userName: value }))
    }
    SignUp = async (ev) => {
        ev.preventDefault()
        const userName = this.state.userName
        await userService.signUp(userName)
        console.log('signed up!')
        this.props.history.push('/homepage')


    }


    render() {
        // if (this.state.users != []) {
        //     //   this.props.history.push('/homepage')
        //     return (<h1>{this.state.users}</h1>)
        // }
        return (
            <div className="signup-container">
                 <h1>Sign up!</h1>
                <form > 
                    <label htmlFor="userName"><p>please type in your name</p></label>
                    <input name='userName' type="text" value={this.state.userName} onChange={this.handleChange} />
                    <button onClick={this.SignUp}>sign up</button>
                </form>
            </div>
        )

        // return (<div>placeholder</div>)
    }
}


