import React, { Component, createRef } from 'react'
import { contactService } from '../services/contact.service'

export class ContactEdit extends Component {
    inputRef = createRef()

    state = {
        contact: null,
        errMsg: '',
    }

    componentDidMount() {
        this.loadContact()
    }

    loadContact= async () => {
        try {
            const {id} = this.props.match.params
            const contact = id ? await contactService.getContactById(id) : contactService.getEmptyContact()
            this.setState({contact})
        } catch (error) {
            const errMsg = `Couldn\'t find contact, ${error}`
            this.setState({errMsg})
        }
    }

    handleChange = ({target})=>{
        const field = target.id
        const value = target.value
        this.setState(prevState=>({contact: {...prevState.contact, [field]:value}}))
    }

    onSaveContact = async (ev) => {
        ev.preventDefault()
        const contact = this.state.contact
        await contactService.saveContact(contact)
        this.props.history.push('/contacts');
    }

    render() {
        const {contact, errMsg} = this.state
        if(!contact) return <div>{errMsg || 'Loading...'}</div>
        const {name, email, phone} = contact
        const header = contact._id ? 'Edit contact' : 'Add new contact'
        return (
            <div>
                <h2>{header}</h2>
                <form className="edit-contact">
                    <label htmlFor="name">Name</label>
                    <input ref={this.inputRef} type="text" id="name" value={name} onChange={this.handleChange}/>
                    
                    <label htmlFor="email">Email</label>
                    <input type="text" id="email" value={email} onChange={this.handleChange}/>
                    
                    <label htmlFor="phone">Phone number</label>
                    <input type="text" id="phone" value={phone} onChange={this.handleChange}/>
                    
                    <button onClick={this.onSaveContact}>Save</button>
                </form>

            </div>
        )
    }
}
